import React, { Component } from 'react'
import axios from 'axios'

export default class LatestPhotos extends Component {
    state = {
        photos : [],
        page:1,
        pageloading:true
    }
    componentDidMount(){
        axios.get('https://api.unsplash.com/photos/?client_id=52eb7a967de615e8d250f0be9f8f428aea5aff280162da4852dd54869f709cc4&per_page=16&page='+ this.state.page).then(res => this.setState({
            photos: res.data,
            pageloading:false,
            page:this.state.page + 1
        }))
    }

    loadNextPage = (e) =>{
        axios.get('https://api.unsplash.com/photos/?client_id=52eb7a967de615e8d250f0be9f8f428aea5aff280162da4852dd54869f709cc4&per_page=16&page='+ this.state.page).then(res => this.setState({
            photos: res.data,
            pageloading:false,
            page:this.state.page + 1
        }))
      }
    render() {
        console.log(this.state.photos)
    
    if (this.state.pageloading === true){
        return (
            <div className="col text-center">Loading</div>
        )
    }
   
    return ( 
        <React.Fragment>
    {
        this.state.photos.map((photo)=>(
            <div key={photo.id} className="col-lg-3">
                <div className="single-photo-itme">
                    <a href={photo.urls.thumb} className="d-block">
                    <div className="photo-wraper">
                        <img src={photo.urls.small} alt={photo.alt_description}/>
                    </div>
                        <h5>{photo.user.name}</h5>
                        <p className="cate-name">
                            By {photo.user.last_name}
                        </p>
                    </a>
                </div>
            </div>
        ))
    }
        <div className="col-lg-12 text-center">
            <div className="load-more-btm">
               <button onClick={this.loadNextPage}>Load More Image {this.state.page}</button>
            </div>
        </div>
        </React.Fragment>

        )
    }
}
